<template lang="">
  <div class="hero" :style="changeBackground()">
    <!--
    <div class="blurred-box">
      <div class="user-login-box">
        <div class="user-title">Associate Professor</div>
        <span class="user-icon"></span>
        <div class="user-name">Camilla Barbarossa</div>
      </div>
    </div>
    -->
  </div>
</template>
<script>
export default {
  name: "HeroComp",
  data() {
    return {
      image: [
        { 'background-image': 'url(' + require(`../assets/img/nature16.jpg`) + ')' }, //0
        { 'background-image': 'url(' + require(`../assets/img/nature21.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature20.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature27.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature8.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature24.jpg`) + ')' },//5
        { 'background-image': 'url(' + require(`../assets/img/nature23.jpg`) + ')' }, 
        { 'background-image': 'url(' + require(`../assets/img/nature15.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature13.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature22.jpg`) + ')' },
        { 'background-image': 'url(' + require(`../assets/img/nature25.jpg`) + ')' }, //10
        { 'background-image': 'url(' + require(`../assets/img/nature18.jpg`) + ')' },
      ],
    };
  },
  mounted() {},
  methods: {
    changeBackground() {
      var hours = new Date().getHours();
      console.log(hours);
      if (hours >= 6 && hours < 8) {
        return this.image[0];
      } else if (hours >= 8 && hours < 10) {
        return this.image[1];
      } else if (hours >= 10 && hours < 11) {
        return this.image[2];
      } else if (hours >= 11 && hours < 11) {
        return this.image[3];
      } else if (hours >= 12 && hours < 14) {
        return this.image[4];
      } else if (hours >= 14 && hours < 16) {
        return this.image[5];
      } else if (hours >= 16 && hours < 17) {
        return this.image[6];
      } else if (hours >= 17 && hours < 18) {
        return this.image[7];
      } else if (hours >= 18 && hours < 19) {
        return this.image[8];
      } else if (hours >= 19 && hours < 20) {
        return this.image[9];
      } else if (hours >= 20 && hours < 22) {
        return this.image[10];
      } else {
        return this.image[11];
      }
    },
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/style/app.scss";

.hero {
  @include flex($justify-content: center, $align-items: center);
  position: relative;
  height: calc(100vh - 50px);
  width: 100%;
  //background-image: url("../assets/img/nature6.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow: hidden;
  @include breakpoint(desktop) {
    height: calc(100vh - 40px);
  }
  .blurred-box {
    height: 300px;
    width: 300px;
    background: inherit;
    border: 1px solid rgba(43, 43, 43, 0.03);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before {
      position: absolute;
      content: "";
      background: inherit;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      box-shadow: inset 0 0 0 200px rgba(255, 255, 255, 0.05);
      filter: blur(8px);
      border-radius: 50%;
    }
    .user-login-box {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;
      z-index: 1;
      .user-icon {
        display: inline-block;
        width: 200px;
        width: 100px;
        height: 100px;
        position: relative;
        border-radius: 50%;
        background-size: contain;
        background-image: url("../assets/img/Camilla-Barbarossa.jpg");
      }
      .user-title {
        color: transparent;
      }
      .user-name {
        color: white;
      }
      .user-title,
      .user-name {
        display: inline-block;
        width: 200px;
        margin-top: 15px;
        margin-bottom: 15px;
      }
    }
  }
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
}
</style>
