<template>
  <div class="home">
    <HeroComp />
    <div class="gs_container">
      <div class="bienvenue">
        <h1>{{ greeting() }}</h1>
        <div class="date">
          <p>{{ currentDate() }}</p>
        </div>
        <p class="news">
          Camilla Barbarossa is Associate Professor of Marketing at TBS. She
          holds a PhD in Business Administration and Management obtained at
          Faculty of Economics, Sapienza University of Rome. Her primary
          research interests concern: (i) consumer adoption of eco-friendly
          alternatives; (ii) consumer responses to corporate social
          responsibility and irresponsibility; and (iii) intergroup bias in
          group judgment processes. She published articles in, among others,
          Journal of Business Ethics, International Marketing Review, Journal of
          Environmental Psychology, Ecological Economics. 
          <br>
          <br>
          <b>Teaching areas :</b>
          marketing, business communication, marketing research.
          <br>
          <br>
          <b>Research areas :</b>
          Ethical and pro-environmental consumption, Consumer responses to
          corporate social responsibility (CSR) and irresponsibility (CSI),
          Effects of stereotypes on intergroup judgments; Country-of-origin
          effects and national stereotypes, Quantitative methods.
          <br>
          <br>
          <b>Proposed expertises :</b>
          ethical consumption, marketing.
        </p>
      </div>
    </div>
    <ContactComp />
  </div>
</template>
<script>
import HeroComp from "../components/HeroComp.vue";
import ContactComp from "../components/ContactComp.vue";

export default {
  name: "HomeView",
  components: {
    HeroComp,
    ContactComp,
  },
  data() {
    return {};
  },
  methods: {
    currentDate() {
      const current = new Date();
      switch (current.getDay()) {
        case 0:
          return `Sunday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 1:
          return `Monday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 2:
          return `Tuesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 3:
          return `Wednesday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 4:
          return `Thursday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 5:
          return `Friday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        case 6:
          return `Saturday, ${current.getDate()} / ${
            current.getMonth() + 1
          } / ${current.getFullYear()}`;
        default:
          "";
          break;
      }
    },
    greeting() {
      var hours = new Date().getHours();
      console.log(hours);
      if (hours >= 6 && hours < 12) {
        return "Good Morning";
      } else if (hours >= 12 && hours < 18) {
        return "Good Afternoon";
      } else if (hours >= 18 && hours < 23) {
        return "Good Evening";
      } else {
        return "Welcome";
      }
    },
  },
};
</script>

